import { faReply, faUserCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import dayjs from "dayjs";
import parser from "bbcode-to-react";
function PostReply({ handleClickReplyBtn, messContent, time, username }) {
  return (
    <li className="reply-post">
      <div className="reply-post__time"> {dayjs(time).format("HH:ss DD-MM-YYYY")} </div>
      <div className="reply-group">
        <div className="reply-group__actions">
          <div
            className="post__controls__icon-btn"
            onClick={() => handleClickReplyBtn(messContent)}
          >
            <FontAwesomeIcon icon={faReply} />
          </div>
        </div>
        <div className="reply-group__content">
          {messContent.indexOf("[img]") !== -1 && messContent.indexOf("[/img]") !== -1 ? (
            <>{parser.toReact(messContent)}</>
          ) : (
            <p>{messContent}</p>
          )}
        </div>
        <div className="reply-group__avatar">
          <div className="reply-group__avatar__icon">
            <img src={`https://avatars.dicebear.com/api/human/${username}.svg`} alt="" />
          </div>
        </div>
      </div>
    </li>
  );
}

PostReply.propTypes = {};

export default PostReply;
