// import { JANUS_PLUGIN } from "../../configs";
import Janus from "./janus";

export function publishVideoCall(janus, opaqueId, username, callback) {
  let videocall;

  if (!janus) {
    return;
  }

  janus.attach({
    plugin: "janus.plugin.videocall",
    opaqueId: opaqueId,
    success: function (pluginHandle) {
      videocall = pluginHandle;
      console.log("videocall success");
      let body = {
        request: "register",
        username,
      };
      videocall.send({ message: body });
    },
    error: function (error) {
      console.log("  -- Error attaching plugin...", error);
    },
    consentDialog: function (status) {
      // Darken screen and show hint
    },
    mediaState: function (type, on) {
      // detect change in media
    },
    onmessage: function (msg, jsep) {
      console.log(" ::: Got a message :::", msg);
      let result = msg["result"];
      if (result) {
        if (result["list"]) {
          var list = result["list"];
          Janus.debug("Got a list of registered peers:", list);
          for (var mp in list) {
            Janus.debug("  >> [" + list[mp] + "]");
          }
        } else if (result["event"]) {
          callback(videocall, result, jsep);
        }
      }
    },
    onlocalstream: function (stream) {
      Janus.attachMediaStream(document.querySelector("#localAudio"), stream);
      videocall.send({ message: { request: "set", audio: true } });
    },
    onremotestream: function (stream) {
      Janus.attachMediaStream(document.querySelector("#remoteAudio"), stream);
      videocall.send({ message: { request: "set", audio: true } });
    },
  });
}

export function doCall(username, videocallHandler) {
  let doSimulcast = true;

  // Call this user
  videocallHandler.createOffer({
    media: { data: true, video: false, audio: true },
    simulcast: doSimulcast,
    success: function (jsep) {
      let body = { request: "call", username };
      videocallHandler.send({ message: body, jsep: jsep });
    },
    error: function (error) {},
  });
}

export function doHangup(videocallHandler) {
  console.log("88888 === doHangup");
  let hangup = { request: "hangup" };
  videocallHandler.send({ message: hangup });
  videocallHandler.hangup();
}

export function doAccept(videocallHandler, jsep) {
  console.log("88888 === doAccept");
  console.log(videocallHandler);
  console.log(jsep);
  let doSimulcast = false;

  videocallHandler.createAnswer({
    jsep,
    media: { videoSend: false, audioSend: true },
    simulcast: doSimulcast,
    success: function (jsep) {
      console.log("doaccept");
      console.log(jsep);
      let body = { request: "accept" };
      videocallHandler.send({ message: body, jsep: jsep });
    },
    error: function (error) {
      console.log("error");
    },
  });
}
