import { JANUS_CONNECT } from "./types";
const initialState = {
  isConnected: false,
  janusInstance: null,
};

function janusReducer(state = initialState, action) {
  switch (action.type) {
    case JANUS_CONNECT.REQUEST:
      return {
        ...state,
      };
    case JANUS_CONNECT.SUCCESS:
      return {
        ...state,
        isConnected: true,
        janusInstance: action.payload.janusInstance,
      };
    case JANUS_CONNECT.ERROR:
      return {
        ...state,
      };

    default:
      return {
        ...state,
      };
  }
}

export default janusReducer;
