import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import PostReply from "./PostReply";
import PostSent from "./PostSent";
import { useElementOnScreen } from "../../../utils/useElementOnScreen";
function Posts({ handleClickReplyBtn, list }) {
  const { listCount } = useSelector((state) => state.chat);
  const [containerRef, isInter] = useElementOnScreen({
    root: null,
    rootMargin: "0px",
    threshold: 1.0,
  });

  useEffect(() => {
    if (isInter) {
      console.log("inter true");
    } else {
      console.log("inter false");
    }
  }, [isInter]);

  useEffect(() => {
    var scrollChatPost = document.querySelector(".chat-tab__posts");
    if (scrollChatPost) scrollChatPost.scrollTop = scrollChatPost.scrollHeight;
  }, [list]);

  return (
    <div className="chat-tab__posts">
      <ul>
        {listCount > 0 &&
          list.map((message) => {
            if (message.partner_id == message.room_id?.split("-")[0]) {
              return (
                <PostReply
                  key={message.id}
                  time={message.time}
                  messContent={message.text}
                  username={message.partner_id + "-" + message.partner_user_id + "-" + message.from}
                  handleClickReplyBtn={handleClickReplyBtn}
                />
              );
            } else {
              return (
                <PostSent
                  key={message.id}
                  time={message.time}
                  messContent={message.text}
                  username={message.partner_id + "-" + message.partner_user_id + "-" + message.from}
                  handleClickReplyBtn={handleClickReplyBtn}
                />
              );
            }
          })}
      </ul>
    </div>
  );
}

Posts.propTypes = {};

export default Posts;
