import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";

function AddMsgReply({ handleCancelAddMsgReply, messReplyContent }) {
  return (
    <div className="add-msg__reply">
      <div className="add-msg__container">
        <div className="add-msg__reply__title">
          Đang trả lời <strong> Nhân viên CSKH </strong>
        </div>
        <p className="add-msg__reply__content"> {messReplyContent} </p>
      </div>
      <div className="add-msg__cancel" onClick={handleCancelAddMsgReply}>
        <FontAwesomeIcon icon={faTimes} />
      </div>
    </div>
  );
}

AddMsgReply.propTypes = {};

export default AddMsgReply;
