import React from "react";
import ChatWidget from "./components/ChatWidget";
import { USER_NAME, JANUS_SERVER, FROM_NAME } from "./web.config";

function App(props) {
  return (
    <>
      <ChatWidget
        position="left"
        opaqueId="test4"
        username={USER_NAME}
        server={JANUS_SERVER}
        isPublisher={false}
        display={FROM_NAME}
      />
    </>
  );
}

export default App;
