import { CHAT_NEW_MESSAGE, CHAT_SCROLLLOAD, REQUIRE_ROOM } from "./types";
const initialState = {
  room: "",
  listCount: 0,
  list: [],
  scrollLoadStatus: {},
};

function chatReducer(state = initialState, action) {
  switch (action.type) {
    case REQUIRE_ROOM.REQUEST:
      return {
        ...state,
      };
    case REQUIRE_ROOM.SUCCESS:
      return {
        ...state,
        room: action.payload.room,
      };
    case REQUIRE_ROOM.ERROR:
      return {
        ...state,
      };
    case CHAT_NEW_MESSAGE.REQUEST:
      return {
        ...state,
      };
    case CHAT_NEW_MESSAGE.SUCCESS:
      let { list } = state;
      list.push(action.payload.message);
      return {
        ...state,
        list,
      };
    case CHAT_NEW_MESSAGE.ERROR:
      return {
        ...state,
      };
    case CHAT_SCROLLLOAD.REQUEST:
      return {
        ...state,
      };
    case CHAT_SCROLLLOAD.SUCCESS:
      return {
        ...state,
        listCount: action.payload.listCount,
        list: action.payload.list.reverse(),
        scrollLoadStatus: action.payload.scrollLoadStatus,
      };
    case CHAT_SCROLLLOAD.ERROR:
      return {
        ...state,
      };
    default:
      return {
        ...state,
      };
  }
}

export default chatReducer;
