import { CHAT_SCROLLLOAD, CHAT_NEW_MESSAGE, REQUIRE_ROOM } from "./types";
import { chatScrollLoad, newMessage } from "../../apis/chat";
import { requireRoom } from "../../apis/room";
import dayjs from "dayjs";
import { v4 as uuidv4 } from "uuid";

export const requireRoomAction = (dataBody) => async (dispatch, getState) => {
  dispatch({ type: REQUIRE_ROOM.REQUEST });

  console.log("***** action: requireRoomAction ****");
  try {
    console.log("==== dataBody ====");
    console.log(dataBody);
    const responseRoom = await requireRoom(dataBody);
    console.log(responseRoom);

    if (responseRoom.status) {
      dispatch({ type: REQUIRE_ROOM.SUCCESS, payload: { room: responseRoom.data.data.room } });
    } else {
      dispatch({
        type: REQUIRE_ROOM.ERROR,
        error: "error",
      });
    }
  } catch (error) {
    dispatch({
      type: REQUIRE_ROOM.ERROR,
      error,
    });
  }
};

export const newMessageChatWidget = (data) => async (dispatch, getState) => {
  dispatch({ type: CHAT_NEW_MESSAGE.REQUEST });

  console.log("***** action: newMessageChatWidget ****");
  try {
    console.log("====== action: newMessage RAW =====");
    console.log(data);

    const responseMessage = await newMessage(data);

    if (responseMessage.status) {
      console.log("====* newMessageChatWidget *====");
      console.log(responseMessage);

      dispatch({
        type: CHAT_NEW_MESSAGE.SUCCESS,
        payload: {
          message: responseMessage.data.data,
        },
      });
    } else {
      dispatch({
        type: CHAT_NEW_MESSAGE.ERROR,
        error: "error",
      });
    }
  } catch (error) {
    dispatch({
      type: CHAT_NEW_MESSAGE.ERROR,
      error,
    });
  }
};

export const chatScrollLoadAction = () => async (dispatch, getState) => {
  console.log("===**** getState");
  const state = getState();

  dispatch({ type: CHAT_SCROLLLOAD.REQUEST });

  try {
    const params = {
      room: state.chat.room,
    };
    console.log("params")
    console.log(params)
    const responseChat = await chatScrollLoad(params);

    if (responseChat.status) {
      const {
        data: { count, list },
        status: scrollLoadStatus,
      } = responseChat.data;
      console.log("0000* chatScrollLoadAction")
      console.log(responseChat.data)

      dispatch({
        type: CHAT_SCROLLLOAD.SUCCESS,
        payload: {
          listCount: count,
          list,
          scrollLoadStatus,
        },
      });
    } else {
      // dispatch({ type: CHAT_SCROLLLOAD.ERROR, payload: { error } });
    }
  } catch (error) {
    dispatch({ type: CHAT_SCROLLLOAD.ERROR, payload: { error } });
  }
};
