import { faCamera, faPaperPlane } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import dayjs from "dayjs";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { v4 as uuidv4 } from "uuid";
import { sendData } from "../../../utils/Janus/chatroom";
import AddMsgReply from "./AddMsgReply";

function randomString(len, charSet) {
  charSet = charSet || "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  var randomString = "";
  for (var i = 0; i < len; i++) {
    var randomPoz = Math.floor(Math.random() * charSet.length);
    randomString += charSet.substring(randomPoz, randomPoz + 1);
  }
  return randomString;
}
function AddMsgBox({
  addMsgReplyVisible,
  handleCancelAddMsgReply,
  messReplyContent,
  chatroomHandler,
}) {
  const dispatch = useDispatch();

  const [currentMessage, setCurrentMessage] = useState("");

  const { isConnected } = useSelector((state) => state.janus);
  const { room: chatroom, listCount, list } = useSelector((state) => state.chat);

  const onMessageSend = () => {
    if (currentMessage && isConnected) {
      const dataJanus = JSON.stringify({
        id: uuidv4(),
        text: currentMessage,
        room: chatroom,
        time: dayjs().valueOf(),
        event: "sent",
      });

      sendData(chatroomHandler, chatroom, dataJanus, dispatch);

      setCurrentMessage("");
    }
  };

  const onKeyUp = (e) => {
    if (e.keyCode === 13) {
      e.preventDefault();
      document.querySelector(".add-msg__send").click();
    }
  };

  return (
    <div className="add-msg">
      {addMsgReplyVisible ? (
        <AddMsgReply
          handleCancelAddMsgReply={handleCancelAddMsgReply}
          messReplyContent={messReplyContent}
        />
      ) : (
        ""
      )}
      <div className="add-msg__wrap">
        <div className="add-msg__camera">
          <input type="file" title="" accept="image/*" />
          <FontAwesomeIcon icon={faCamera} />
        </div>
        <input
          className="add-msg__message"
          type="text"
          value={currentMessage}
          onChange={(e) => setCurrentMessage(e.target.value)}
          onKeyUp={onKeyUp}
        />
        <div className="add-msg__send" onClick={onMessageSend}>
          <FontAwesomeIcon icon={faPaperPlane} />
        </div>
      </div>
    </div>
  );
}

AddMsgBox.propTypes = {};

export default AddMsgBox;
