import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";

function ChatTabSearchBar(props) {
  return (
    <div className="chat-tab__title__search-bar">
      <div className="chat-tab__title__search-bar__wrap-input">
        <input type="search" placeholder="Search" />
        {/* <button type="button" className="chat-tab__title__search-bar__times-btn">
          <FontAwesomeIcon icon={faTimes} />
        </button> */}
      </div>
      <p className="chat-tab__title__search-bar__result"> 1 / 2 kết quả cho "khach hang" </p>
    </div>
  );
}

ChatTabSearchBar.propTypes = {};

export default ChatTabSearchBar;
