import { faMinus, faPhoneAlt, faSearch } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import ChatTabSearchBar from "./ChatTabSearchBar";

function ChatTabGroupTitle({
  handleClickPhoneBtn,
  handleClickSeachBtn,
  handleClickMinimizedIcon,
  searchBarVisible,
}) {
  return (
    <div className="chat-tab__title">
      <div className="chat-tab__title__group">
        <h2> Chat với nhân viên CSKH </h2>
        <ul className="chat-tab__actions">
          <li className="items" id="phoneBtn" onClick={handleClickPhoneBtn}>
            <FontAwesomeIcon icon={faPhoneAlt} />
          </li>
          <li className="items" onClick={handleClickSeachBtn}>
            <FontAwesomeIcon icon={faSearch} />
          </li>
          <li className="items" onClick={handleClickMinimizedIcon}>
            <FontAwesomeIcon icon={faMinus} />
          </li>
        </ul>
      </div>
      {searchBarVisible ? <ChatTabSearchBar /> : ""}
    </div>
  );
}

ChatTabGroupTitle.propTypes = {};

export default ChatTabGroupTitle;
