import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { chatScrollLoadAction, requireRoomAction } from "../../reducer/ChatWidget/actions";
import { CHAT_NEW_MESSAGE } from "../../reducer/ChatWidget/types";
import { JANUS_CONNECT } from "../../reducer/Janus/types";
import { publishChatroom } from "../../utils/Janus/chatroom";
import Janus from "../../utils/Janus/janus";
import { doAccept, doCall, doHangup, publishVideoCall } from "../../utils/Janus/videocall";
import { FROM_NAME, FROM_USER_ID, TO_CALL_PARTNER_USERNAME, TO_PARTNER_ID } from "../../web.config";
import ChatTab from "./components/ChatTab";

function ChatWidget({ position, opaqueId, username, server, isPublisher, display }) {
  const dispatch = useDispatch();

  const [chatTabVisible, setChatTabVisible] = useState(false);
  const [searchBarVisible, setSearchBarVisible] = useState(false);
  const [callModalVisible, setCallModalVisible] = useState(false);
  const [addMsgReplyVisible, setAddMsgReplyVisible] = useState(false);

  const [janusInstance, setJanusInstance] = useState(null);

  const [chatroomHandler, setChatroomHandler] = useState(null);

  const [messReplyContent, setMessReplyContent] = useState("");
  const [videocallHandler, setVideocallHandler] = useState(null);
  const [callStatus, setCallStatus] = useState(null);
  const [jsep, setJsep] = useState(null);

  const { room: chatroom } = useSelector((state) => state.chat);

  useEffect(() => {
    const dataBody = {
      from_user_id: FROM_USER_ID,
      from_name: FROM_NAME,
      to_partner_id: TO_PARTNER_ID,
    };
    dispatch(requireRoomAction(dataBody));
  }, []);

  useEffect(() => {
    if (chatroom) {
      Janus.init({
        debug: "all",
        callback: function () {
          if (!Janus.isWebrtcSupported()) {
            console.log("No WebRTC support... ");
            return;
          }

          const janus = new Janus({
            server: server,
            success: function () {
              // Attach to echo test plugin
              console.log("Janus loaded");
              setJanusInstance(janus);
              dispatch(chatScrollLoadAction());
              dispatch({ type: JANUS_CONNECT.SUCCESS, payload: { janusInstance: janus } });
            },
            error: function (error) {
              Janus.error(error);
              setJanusInstance(null);
            },
            destroyed: function () {
              setJanusInstance(null);
            },
          });
        },
      });
    }
  }, [chatroom]);

  useEffect(() => {
    publishChatroom(
      janusInstance,
      opaqueId,
      isPublisher,
      chatroom,
      username,
      display,
      (_chatroomHandler, eventType, data) => {
        console.log("===== DATA RETURN =====");
        console.log(data);
        console.log(eventType);
        if (eventType === "created") {
          console.log("==== created ====");
        } else if (eventType === "joined") {
          console.log("==== joined ====");
          setChatroomHandler(_chatroomHandler);
        } else if (eventType === "ondata") {
          console.log("==== ondata ====");
          console.log(data);
          if (data.from != username) {
            dispatch({
              type: CHAT_NEW_MESSAGE.SUCCESS,
              payload: {
                message: {
                  room_id: data.room,
                  from: data.from,
                  text: data.data,
                  time: data.date,
                  partner_id: data.room.split("-")[1],
                  partner_user_id: data.room.split("-")[2],
                },
              },
            });
          }
        }
      }
    );

    console.log("==== publishVideoCall ====");
    publishVideoCall(janusInstance, opaqueId, username, (_videocallHandler, result, _jsep) => {
      console.log("==== DATA VIDEO CALL RETURN ====");
      console.log(result);
      setJsep(_jsep);
      const eventResult = result["event"];
      if (eventResult === "registered") {
        _videocallHandler.send({ message: { request: "list" } });
        setCallStatus("registered");
        setVideocallHandler(_videocallHandler);
      } else if (eventResult === "calling") {
        setCallStatus("calling");
      } else if (eventResult === "incomingcall") {
        setCallModalVisible(true);
        setCallStatus("incomingcall");
      } else if (eventResult === "accepted") {
        setCallStatus("accepted");
        if (_jsep) _videocallHandler.handleRemoteJsep({ jsep: _jsep });
      } else if (eventResult === "update") {
      } else if (eventResult === "hangup") {
        setCallModalVisible(false);
      }
    });
  }, [janusInstance]);

  const handleClickChatBox = () => {
    if (chatTabVisible) {
      setChatTabVisible(false);
    } else {
      setChatTabVisible(true);
    }
  };

  const handleClickMinimizedIcon = () => {
    setChatTabVisible(false);
  };

  const handleClickSeachBtn = () => {
    if (searchBarVisible) {
      setSearchBarVisible(false);
    } else {
      setSearchBarVisible(true);
    }
  };

  const handleClickPhoneBtn = () => {
    doCall(TO_CALL_PARTNER_USERNAME, videocallHandler);
    setCallModalVisible(true);
  };

  const handleClickReplyBtn = (messContent) => {
    setMessReplyContent(messContent);
    setAddMsgReplyVisible(true);
  };

  const handleCancelAddMsgReply = () => {
    setAddMsgReplyVisible(false);
  };

  const handleClickCancelBtn = () => {
    console.log("handleClickCancelBtn");
    doHangup(videocallHandler);
    setCallModalVisible(false);
  };
  const handleClickAcceptBtn = () => {
    console.log("handleClickAcceptBtn");
    doAccept(videocallHandler, jsep);
  };

  return (
    <div className="chat-widget" style={{ right: position === "right" ? "0" : "" }}>
      <div className="chat-widget__icon-wrap">
        <div className="chat-widget__icon" onClick={handleClickChatBox}></div>
      </div>
      {chatTabVisible ? (
        <ChatTab
          handleClickPhoneBtn={handleClickPhoneBtn}
          handleClickSeachBtn={handleClickSeachBtn}
          handleClickMinimizedIcon={handleClickMinimizedIcon}
          handleClickReplyBtn={handleClickReplyBtn}
          handleCancelAddMsgReply={handleCancelAddMsgReply}
          handleClickCancelBtn={handleClickCancelBtn}
          handleClickAcceptBtn={handleClickAcceptBtn}
          searchBarVisible={searchBarVisible}
          addMsgReplyVisible={addMsgReplyVisible}
          messReplyContent={messReplyContent}
          position={position}
          chatroomHandler={chatroomHandler}
          callModalVisible={callModalVisible}
        />
      ) : (
        ""
      )}
    </div>
  );
}

export default ChatWidget;
