import { faMicrophone, faMicrophoneSlash, faPhoneAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import dayjs from "dayjs";
import React, { useEffect, useState } from "react";
import { TO_CALL_PARTNER_USERNAME } from "../../../web.config";

function CallAlert({ handleClickCancelBtn, handleClickAcceptBtn }) {
  const [currentCallTime, setCurrentCallTime] = useState(null);
  const [useMicrophone, setUseMicrophone] = useState(true);

  const customCss = {
    border: "1px solid",
    borderRadius: "50%",
    width: "50px",
    height: "50px",
  };

  useEffect(() => {
    setCurrentCallTime(null);
  }, []);

  const handleTimeUpdate = (track) => {
    let currentTime = Math.floor(track.timeStamp);
    let timeFormat = dayjs(currentTime).format("mm:ss");
    setCurrentCallTime(timeFormat);
  };

  const handleClickMicrophoneBtn = () => {
    setUseMicrophone(!useMicrophone);
  };

  return (
    <div className="call-alert">
      <div className="call-alert__wrap">
        <div className="call-alert__user">
          <div className="call-alert__user__avatar">
            <img
              src={`https://avatars.dicebear.com/api/human/${TO_CALL_PARTNER_USERNAME}.svg`}
              alt=""
            />
          </div>
          <div className="call-alert__user__name">Nguyễn Trung Đức</div>
        </div>
        <div className="call-alert__status">
          {currentCallTime ? (
            <p className="call-alert__time">{currentCallTime}</p>
          ) : (
            <>Đang gọi...</>
          )}
          <div>
            <audio id="localAudio" controls autoPlay playsInline muted></audio>
            <audio
              id="remoteAudio"
              controls
              autoPlay
              playsInline
              onTimeUpdate={handleTimeUpdate}
            ></audio>
          </div>
        </div>
        <div className="call-alert__controls">
          {currentCallTime ? (
            <>
              <button
                className="call-alert__controls__item"
                id="cancelBtn"
                onClick={handleClickCancelBtn}
                style={customCss}
              >
                <div className="call-alert__controls__item__icon__cancel">
                  <FontAwesomeIcon icon={faPhoneAlt} />
                </div>
              </button>
              <button
                className="call-alert__controls__item"
                id="microphoneBtn"
                onClick={handleClickMicrophoneBtn}
                style={customCss}
              >
                <div className="call-alert__controls__item__icon__microphone">
                  <FontAwesomeIcon icon={useMicrophone ? faMicrophone : faMicrophoneSlash} />
                </div>
              </button>
            </>
          ) : (
            <>
              <button
                className="call-alert__controls__item"
                id="cancelBtn"
                onClick={handleClickCancelBtn}
              >
                <div className="call-alert__controls__item__icon__cancel">
                  <FontAwesomeIcon icon={faPhoneAlt} />
                </div>
                Cancel
              </button>
              <button
                className="call-alert__controls__item"
                id="acceptBtn"
                onClick={handleClickAcceptBtn}
              >
                <div className="call-alert__controls__item__icon__accept">
                  <FontAwesomeIcon icon={faPhoneAlt} />
                </div>
                Accept
              </button>
            </>
          )}
        </div>
      </div>
    </div>
  );
}

CallAlert.propTypes = {};

export default CallAlert;
