import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import AddMsgBox from "./AddMsgBox";
import ChatTabGroupTitle from "./ChatTabGroupTitle";
import ChatTabSearchBar from "./ChatTabSearchBar";
import Posts from "./Posts";
import { useElementOnScreen } from "../../../utils/useElementOnScreen";
import CallAlert from "./CallAlert";

function ChatTab({
  handleClickPhoneBtn,
  handleClickSeachBtn,
  handleClickMinimizedIcon,
  searchBarVisible,
  handleClickReplyBtn,
  addMsgReplyVisible,
  handleCancelAddMsgReply,
  messReplyContent,
  position,
  chatroomHandler,
  callModalVisible,
  handleClickCancelBtn,
  handleClickAcceptBtn,
}) {
  const { listCount, list } = useSelector((state) => state.chat);
  // const [containerRef, isInter] = useElementOnScreen({
  //   root: null,
  //   rootMargin: "0px",
  //   threshold: 1.0,
  // });

  // const { list } = useSelector((state) => state.chat);

  // useEffect(() => {
  //   var scrollChatPost = document.querySelector(".chat-tab__posts");
  //   if (scrollChatPost) scrollChatPost.scrollTop = scrollChatPost.scrollHeight;
  // }, [list, messages]);

  // useEffect(() => {
  //   if (isInter) {
  //     console.log("inter true");
  //   } else {
  //     console.log("inter false");
  //   }
  // }, [isInter]);

  return (
    <div
      className="chat-tab"
      style={
        position === "right" ? { marginRight: "3.4375rem", right: 0 } : { marginLeft: "3.4375rem" }
      }
    >
      <ChatTabGroupTitle
        handleClickPhoneBtn={handleClickPhoneBtn}
        handleClickSeachBtn={handleClickSeachBtn}
        handleClickMinimizedIcon={handleClickMinimizedIcon}
        searchBarVisible={searchBarVisible}
      />

      {callModalVisible ? (
        <CallAlert
          handleClickCancelBtn={handleClickCancelBtn}
          handleClickAcceptBtn={handleClickAcceptBtn}
        />
      ) : (
        <Posts handleClickReplyBtn={handleClickReplyBtn} list={list} />
      )}

      <AddMsgBox
        messReplyContent={messReplyContent}
        addMsgReplyVisible={addMsgReplyVisible}
        handleCancelAddMsgReply={handleCancelAddMsgReply}
        chatroomHandler={chatroomHandler}
      />
    </div>
  );
}

ChatTab.propTypes = {};

export default ChatTab;
