import axios from "../utils/request";

const prefix = "/api/v1/chat";

export const newMessage = (data) => {
  return axios({
    method: "POST",
    url: `${prefix}/new`,
    data,
  });
};

export const chatScrollLoad = (params = {}) => {
  return axios({
    method: "GET",
    url: `${prefix}/scrollLoad`,
    params: {
      ...params,
    },
  });
};

export const chatSearch = ({ raw }) => {
  return axios({
    method: "GET",
    url: `${prefix}/search`,
    data: {
      ...raw,
    },
  });
};
